(function() {
  angular.module('mnoApiJs').directive('acmeNavbar', function() {
    var NavbarController, directive;
    NavbarController = function(moment) {
      'ngInject';
      var vm;
      vm = this;
      vm.relativeDate = moment(vm.creationDate).fromNow();
    };
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
        creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };
  });

}).call(this);


(function() {
  angular.module('mnoApiJs').directive('acmeMalarkey', function() {
    var MalarkeyController, directive, linkFunc;
    MalarkeyController = function($log, githubContributor) {
      'ngInject';
      var activate, getContributors, vm;
      vm = this;
      activate = function() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      };
      getContributors = function() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;
          return vm.contributors;
        });
      };
      vm.contributors = [];
      activate();
    };
    linkFunc = function(scope, el, attr, vm) {
      var typist, watcher;
      watcher = void 0;
      typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });
      el.addClass('acme-malarkey');
      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause()["delete"]();
      });
      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause()["delete"]();
        });
      });
      scope.$on('$destroy', function() {
        watcher();
      });
    };
    return directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };
  });

}).call(this);


(function() {
  angular.module('mnoApiJs').factory('githubContributor', function($log, $http) {
    'ngInject';
    var apiHost, getContributors, service;
    apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';
    getContributors = function(limit) {
      var getContributorsComplete, getContributorsFailed;
      if (limit == null) {
        limit = 30;
      }
      getContributorsComplete = function(response) {
        return response.data;
      };
      getContributorsFailed = function(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      };
      return $http.get(apiHost + '/contributors?per_page=' + limit).then(getContributorsComplete)["catch"](getContributorsFailed);
    };
    return service = {
      apiHost: apiHost,
      getContributors: getContributors
    };
  });

}).call(this);


(function() {
  this.AppServices.factory('ImpacApiSvc', function($log, $base64, Restangular) {
    return function(apiKey, apiSecret, url) {
      return Restangular.withConfig(function(RestangularProvider) {
        RestangularProvider.setBaseUrl(url);
        return RestangularProvider.setDefaultHeaders({
          'Accept': 'application/json',
          'Authorization': 'Basic ' + $base64.encode(apiKey + ":" + apiSecret)
        });
      });
    };
  });

}).call(this);


(function() {
  this.AppServices.service('EndpointSvc', function() {
    this.apikey = '';
    this.apisecret = '';
    this.groupid = '';
    return this;
  });

}).call(this);


(function() {
  this.AppServices.factory('ConnecApiSvc', function($log, $base64, Restangular) {
    return function(apiKey, apiSecret, url, groupId) {
      return Restangular.withConfig(function(RestangularProvider) {
        RestangularProvider.setBaseUrl(url + "/" + groupId);
        RestangularProvider.setDefaultHeaders({
          'Accept': 'application/json',
          'Content-Type': 'application/vnd.api+json',
          'Authorization': 'Basic ' + $base64.encode(apiKey + ":" + apiSecret)
        });
        return RestangularProvider.addResponseInterceptor(function(data, operation, what, url, response, deferred) {
          var extractedData;
          extractedData = null;
          if (operation === 'getList') {
            extractedData = data[what];
          } else {
            extractedData = data;
          }
          return extractedData;
        });
      });
    };
  });

}).call(this);


(function() {
  this.AppViews.controller('ImpacController', function($log, ImpacApiSvc, EndpointSvc) {
    'ngInject';
    var vm;
    vm = this;
    vm.widgets = [];
    vm.selectedWidget = 'accounts/balance';
    vm.apiEndpoint = 'https://api-impac.maestrano.com/api/v1';
    vm.getWidgets = function() {
      vm.widgets = [];
      vm.widgets.push({
        name: 'Accounts Balance',
        engine: 'accounts/balance'
      });
      vm.widgets.push({
        name: 'Accounts Summary',
        engine: 'accounts/assets_summary'
      });
      vm.widgets.push({
        name: 'Payable / Receivable',
        engine: 'accounts/payable_receivable'
      });
      return vm.widgets.push({
        name: 'EBITDA',
        engine: 'accounts/accounting_values/ebitda'
      });
    };
    vm.fetchData = function() {
      var payload, resourceApi;
      payload = {
        metadata: {
          organization_ids: [EndpointSvc.groupid]
        },
        engine: vm.selectedWidget
      };
      resourceApi = ImpacApiSvc(EndpointSvc.apikey, EndpointSvc.apisecret, vm.apiEndpoint).all('get_widget');
      return resourceApi.post(angular.toJson(payload)).then(function(data) {
        return vm.response = JSON.stringify(data, null, 2);
      });
    };
    vm.changeCredentials = function() {
      if (EndpointSvc.apikey === '' || EndpointSvc.apisecret === '' || EndpointSvc.groupid === '') {
        return vm.alerts = [
          {
            message: 'API Credentials are required'
          }
        ];
      } else {
        return vm.alerts = [];
      }
    };
    vm.getWidgets();
    vm.changeCredentials();
  });

}).call(this);


(function() {
  this.AppViews.controller('ConnecController', function($log, ConnecApiSvc, EndpointSvc) {
    'ngInject';
    var vm;
    vm = this;
    vm.alerts = [];
    vm.endpoints = [];
    vm.selectedResource = 'company';
    vm.apiEndpoint = 'https://api-connec.maestrano.com/api/v2';
    vm.getEndpoints = function() {
      var resourceApi;
      resourceApi = ConnecApiSvc(EndpointSvc.apikey, EndpointSvc.apisecret, vm.apiEndpoint, EndpointSvc.groupid).all('');
      return resourceApi.doGET().then(function(data) {
        vm.endpoints = [];
        return angular.forEach(data['_links'], (function(resourceLink) {
          var resourceName;
          resourceName = Object.keys(resourceLink)[0];
          resourceLink = resourceLink[resourceName]['href'];
          vm.endpoints.push({
            resource: resourceName,
            link: resourceLink
          });
        }));
      });
    };
    vm.getResources = function() {
      var resourceApi;
      resourceApi = ConnecApiSvc(EndpointSvc.apikey, EndpointSvc.apisecret, vm.apiEndpoint, EndpointSvc.groupid).all(vm.selectedResource);
      return resourceApi.doGET().then(function(data) {
        return vm.response = JSON.stringify(data, null, 2);
      });
    };
    vm.createResource = function() {
      var entity_hash, resourceApi;
      entity_hash = {};
      entity_hash[vm.selectedResource] = JSON.parse(vm.payload);
      resourceApi = ConnecApiSvc(EndpointSvc.apikey, EndpointSvc.apisecret, vm.apiEndpoint, EndpointSvc.groupid).all(vm.selectedResource);
      return resourceApi.post(angular.toJson(entity_hash)).then(function(data) {
        return vm.getResources(vm.selectedResource);
      });
    };
    vm.exampleResource = function() {
      var resourceApi;
      resourceApi = ConnecApiSvc(EndpointSvc.apikey, EndpointSvc.apisecret, vm.apiEndpoint, EndpointSvc.groupid).all(vm.selectedResource + "/example");
      return resourceApi.doGET().then(function(data) {
        return vm.payload = JSON.stringify(data[vm.selectedResource], null, 2);
      });
    };
    vm.changeResource = function() {
      vm.getEndpoints();
      return vm.exampleResource();
    };
    vm.changeCredentials = function() {
      if (EndpointSvc.apikey === '' || EndpointSvc.apisecret === '' || EndpointSvc.groupid === '') {
        return vm.alerts = [
          {
            message: 'API Credentials are required'
          }
        ];
      } else {
        return vm.alerts = [];
      }
    };
    vm.changeCredentials();
    vm.changeResource();
  });

}).call(this);


